.box{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 30rem;
  padding: 3.5rem;
  box-sizing: border-box;
  border: 1px solid #dadce0;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.box h2{
  margin: 0px 0 --0.125rem;
  padding: 0;
  text-align: center;
  color: #202124;
  font-size: 24px;
  font-weight: 400;
}

.box .logo{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 16px;
}

.box p {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  line-height: 1.5;
  margin-bottom: 24px;
  text-align: center;
}

.box .inputBox{
  position: relative;
}

.box .inputBox input{
  width: 93%;
  padding: 1.2rem 10px;
  font-size: 1rem;
  letter-spacing: 0.062rem;
  margin-bottom: 1.875rem;
  border: 1px solid #ccc;
  background: transparent;
  border-radius: 4px;
}

.box .inputBox label{
  position: absolute;
  top: 0;
  left: 10px;
  padding: 0.625rem 0;
  font-size: 1rem;
  color: gray;
  pointer-events: none;
  transition: 0.5s;
}

.box .inputBox input:focus ~label,
.box .inputBox input:valid ~label,
.box .inputBox input:not([value=""]) ~label{
  top: -1.125rem;
  left: 10px;
  color: #1a73e8;
  font-size: 0.75rem;
  background-color: #fff;
  height: 10px;
  padding-left: 5px;
  padding-right: 5px;
}

.box .inputBox input:focus{
  outline: none;
  border: 2px solid #1a73e8;
}

.box input[type= "submit"]{
 border: none;
 outline: none;
 color: white;
 background-color: #1a73e8;
 padding: 0.625rem 1.25rem;
 cursor: pointer;
 border-radius: 0.312rem;
 font-size: 1rem;
 float: right;
}